            },
            directionInit() {
                //初始化小车方向
                this.startAngle = Math.atan2(lanLngs[1].lng - lanLngs[0].lng, lanLngs[1].lat - lanLngs[0].lat) * (180 / Math.PI) + 180;
                this.$nextTick(function () {
                    document.getElementsByClassName('car')[0].style.transform = "rotate(" + this.startAngle + "deg)"
                })
            }
        },
        created() {
            var that = this, lanLngs1 = [], mark, myIcon, areaInfo = []
            window.interval = null
            this.$nextTick(() => {
                this.mapArea.setView([lanLngs[0].lat, lanLngs[0].lng], 17)
                // for(let i=0;i<10;i++){
                //     L.marker([lanLngs[i].lat, lanLngs[i].lng], {icon: ''}).addTo(this.mapArea).bindTooltip("my tooltip text")
                // }
                for (let i = 0; i < lanLngs.length; i++) {
                    lanLngs1.push([lanLngs[i].lat, lanLngs[i].lng])
                    areaInfo.push(lanLngs[i].toponymy)

                    this.lineDate[i].status = lanLngs[i].toponymy
                }
                // console.log(areaInfo)
                this.directionInit();
                myIcon = L.divIcon({className: 'my-div-icon', html: '<div class="car"></div>'});
                L.polyline(lanLngs1, {color: 'rgba(252,255,56,0.4)'}).addTo(this.mapArea);
                mark = L.Marker.movingMarker(lanLngs1,
                    20000, {autostart: true, delay: 1000, icon: myIcon, areaDetail: areaInfo}).addTo(this.mapArea);
                mark.on('click', function () {
                    if (mark.isRunning()) {
                        clearInterval(window.interval)
                        mark.pause();
                    } else {
                        mark.start();
                        window.interval = setInterval(function () {
                            console.log(1)
                            if (mark.isEnded()) {
                                clearInterval(window.interval)
                                return false
                            }
                            that.mapArea.setView([mark.getLatLng().lat, mark.getLatLng().lng], 17)
                        }, 30)
                    }
                });
                window.interval = setInterval(function () {
                    console.log(1)
                    if (mark.isEnded()) {
